import { Component, ViewChild } from '@angular/core';
import { Nav, NavParams, Platform, Events, ModalController, ViewController, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ToastController } from 'ionic-angular';

import { CameraPage } from '../pages/camera/camera';
import { IntervalometerPage } from '../pages/intervalometer/intervalometer';
import { ClipsPage } from '../pages/clips/clips';
import { SettingsPage } from '../pages/settings/settings';

import { ViewProvider } from '../providers/view/view';

@Component({
  templateUrl: 'app.html'
})
export class ViewApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = CameraPage;

  pages: Array<{title: string, component: any, icon: string}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public events: Events, private viewApi: ViewProvider, public modalCtrl: ModalController, public alertCtrl: AlertController, public toastCtrl: ToastController) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Camera', component: CameraPage, icon: 'camera' },
      { title: 'Intervalometer', component: IntervalometerPage, icon: 'timer' },
      { title: 'Time-lapse Clips', component: ClipsPage, icon: 'film' },
      { title: 'Settings', component: SettingsPage, icon: 'settings' }
    ];

    let alert: any = null;
    events.subscribe('error', (err) => {
      let wait = 0;
      if(alert) {
        alert.dismiss();
        wait = 500;
      }
      setTimeout(() => {
        alert = this.alertCtrl.create({
          title: 'ERROR',
          subTitle: err.message || err.msg || 'unknown error',
          buttons: ['Close']
        });
        alert.present();
      }, wait);
    });

    let toast: any = null;
    events.subscribe('login_required', () => {
      console.log('login_required event captured');
      let modal = this.modalCtrl.create(LoginModalContentPage, {});
      if(toast) {
        toast.dismiss();
      }
      modal.present();
    });

    events.subscribe('disconnected', (msg) => {
      let wait = 0;
      if(toast) {
        toast.dismiss();
        wait = 1000;
      }
      setTimeout(() => {
        toast = this.toastCtrl.create({
          message: msg,
          position: 'bottom',
          cssClass: 'error-toast'
        });
        toast.present();
      }, wait);
    });

    events.subscribe('connected', (msg) => {
      let wait = 0;
      if(toast) {
        toast.dismiss();
        wait = 1000;
      }
      setTimeout(() => {
        toast = this.toastCtrl.create({
          message: msg,
          position: 'bottom',
          duration: 3000,
          //showCloseButton: true,
        });
        toast.present();
      }, wait);
    });

    events.subscribe('nodevice', (status) => {
      let wait = 0;
      if(toast) {
        toast.dismiss();
        wait = 1000;
      }
      if(!status) return; // device added
      setTimeout(() => {
        toast = this.toastCtrl.create({
          message: "No VIEW device connected.  Connect a paired device to wifi to access or pair a new device.",
          position: 'bottom',
          showCloseButton: true,
          closeButtonText: 'Add new VIEW'
        });
        toast.onDidDismiss(() => {
          if(viewApi.noDevice && viewApi.connected && viewApi.loggedIn) {
            console.log('Adding VIEW device...');
            let modal = this.modalCtrl.create(PairModalContentPage, {});
            modal.present();
          }
        });
        toast.present();
      }, wait);
    });

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}

@Component({
  templateUrl: 'login.modal.html'
})
export class LoginModalContentPage {

  toast:any = null;

  constructor(
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    private toastCtrl: ToastController
  ) {

  }

  login(email, password) {
    this.toast = this.toastCtrl.create({
      message: "Logging in as " + email + "...",
      position: 'bottom',
    });
    this.toast.present();
    this.viewApi.login(email, password).then((res:any) => {
      if(this.toast) {
        this.toast.dismiss();
      }
      console.log("login res", res);
      if(res.action == 'login') {
        this.viewCtrl.dismiss();
      } else if(res.action == 'login_failed') {
        console.log("message:", res.message);
        this.toast = this.toastCtrl.create({
          message: res.message,
          position: 'bottom',
          cssClass: 'error-toast',
          duration: 10000,
        });
        this.toast.present();
      }
    }, ()=> {
      if(this.toast) {
        this.toast.dismiss();
      }
      this.toast = this.toastCtrl.create({
        message: "Error contacting server - please try again later",
        position: 'bottom',
        cssClass: 'error-toast',
        showCloseButton: true,
      });
      this.toast.present();
    });  
  }
}

@Component({
  templateUrl: 'pair.modal.html'
})
export class PairModalContentPage {

  toast:any = null;

  constructor(
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    private toastCtrl: ToastController
  ) {
    console.log("PairModalContentPage initialized")
  }
  
  dismiss() {
    if(this.toast) {
      this.toast.dismiss();
    }
    this.viewApi.noDevice = false;
    this.viewCtrl.dismiss();
  }

  pair(code) {
    this.toast = this.toastCtrl.create({
      message: "Pairing device...",
      position: 'bottom',
    });
    this.toast.present();
    this.viewApi.addDevice(code).then((res:any) => {
      if(this.toast) {
        this.toast.dismiss();
      }
      console.log("login res", res);
      if(res.action == 'device_added') {
        this.viewCtrl.dismiss();
      } else {
        console.log("message:", res.message);
        this.toast = this.toastCtrl.create({
          message: res.message,
          position: 'bottom',
          cssClass: 'error-toast',
          duration: 10000,
        });
        this.toast.present();
      }
    }, ()=> {
      if(this.toast) {
        this.toast.dismiss();
      }
      this.toast = this.toastCtrl.create({
        message: "Error contacting server - please try again later",
        position: 'bottom',
        cssClass: 'error-toast',
        showCloseButton: true,
      });
      this.toast.present();
    });  
  }
}

@Component({
  templateUrl: 'motion.modal.html'
})
export class MotionModalContentPage {
  axes: any = [];
  motion: any = {};
  toast:any = null;
  axisId:any = null;

  constructor(
    public params: NavParams,
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    private toastCtrl: ToastController,
    private events: Events
  ) {
    this.axisId = params.get('axisId');
    if(this.axisId) {
      this.axes = viewApi.motion.axis.filter(a => { return a.id == this.axisId});
    } else {
      this.motion = viewApi.motion;
      this.motion.get();
      events.subscribe('motion.updated', () => {
        this.axes = viewApi.motion.axis || [];
        this.motion = viewApi.motion;
      })
    }

    console.log("MotionModalContentPage initialized", this.axes);
  }
  
  dismiss() {
    for(let i = 0; i < this.axes.length; i++) {
      this.updateHardware(this.axes[i]);
      this.updateType(this.axes[i]);
      this.axes[i].setup = true;
      if(this.axes[i].unit == 's') this.axes[i].unitSteps = 1;
      this.viewApi.db.set('motion-' + this.axes[i].id, this.axes[i]);
      console.log("saving motion setup:", this.axes[i])
      this.motion.axis = this.axes;
    }

    if(this.toast) {
      this.toast.dismiss();
    }
    this.viewApi.noDevice = false;
    this.viewCtrl.dismiss();
  }

  updateHardware(axis) {
      console.log("updating hardware", axis)
      if(axis.driver == 'NMX' && axis.hardware == 'stager') {
          axis.unitSteps = 558.63333334;
      } else if(axis.driver == 'NMX' && axis.hardware == 'sapphire') {
          axis.unitSteps = 550.81967213;
      }
  }
  updateType(axis) {
      console.log("updating driver", axis)
      if(axis.name == 'Pan' || axis.name == 'Tilt') {
          axis.unit = '';
          if(axis.driver == 'NMX') {
              if(axis.hardware == 'stager') {
                  axis.unitSteps = 558.63333334;
              } else if(axis.hardware == 'sapphire') {
                  axis.unitSteps = 550.81967213;
              }
          } else if(axis.driver == 'GM') {
              axis.unitSteps = 1;
          }
      } else {
          axis.unit = 's';
          axis.unitSteps = 1;
      }
  }
}




