import { Component, ViewChild } from '@angular/core';
import { ModalController, NavParams, ViewController, NavController, AlertController, Events } from 'ionic-angular';

import { ViewProvider } from '../../providers/view/view';
import { MotionModalContentPage } from '../../app/app.component';

import { Chart } from 'chart.js';
import moment from 'moment';


@Component({
  selector: 'page-intervalometer',
  templateUrl: 'intervalometer.html'
})
export class IntervalometerPage {
  private intervalHandle:any;
  public camera:any = {};
  public frames:any = {};
  public motion:any = {};
  public intervalometer:any = {
    program: {}
  };
  
  public allowedNonMotorAxes:any = ['interval', 'focus', 'ev'];

  public viewMode: string = "status";

  @ViewChild('chart') chartCanvas;
  chart :any;

  public lists:any = {
    bracketing: [
      {label: 'Single frames', value: 1},
      {label: 'Multi-exposure Sets', value: 3}
    ],
    bracketingFrames: [
      {label: 'sets of 2', value: 2},
      {label: 'sets of 3', value: 3},
      {label: 'sets of 4', value: 4},
      {label: 'sets of 5', value: 5},
      {label: 'sets of 6', value: 6},
      {label: 'sets of 7', value: 7},
      {label: 'sets of 8', value: 8},
      {label: 'sets of 9', value: 9},
      {label: 'sets of 10', value: 10}
    ],
    bracketingStops: [
      {label: '1/3 stop', value: 1/3},
      {label: '2/3 stop', value: 2/3},
      {label: '1 stop', value: 1},
      {label: '1 1/3 stops', value: 1+1/3},
      {label: '1 2/3 stops', value: 1+2/3},
      {label: '2 stops', value: 2},
      {label: '2 1/3 stops', value: 2+1/3},
      {label: '2 2/3 stops', value: 2+2/3},
      {label: '3 stops', value: 3},
      {label: '3 1/3 stops', value: 3+1/3},
      {label: '3 2/3 stops', value: 3+2/3},
      {label: '4 stops', value: 4}
    ],
    panDirection: [
      {label: 'Left', value: 'left'},
      {label: 'Right', value: 'right'}
    ],
    trackingTarget: [
      {label: 'Follow Sun', value: 'sun'},
      {label: 'Follow Moon', value: 'moon'}
    ],
    trackingAxes: [
      {label: 'Pan Only', value: 'pan'},
      {label: 'Tilt Only', value: 'tilt'},
      {label: 'Pan & Tilt', value: 'pan/tilt'}
    ],
    yesNo: [
      {label: 'Yes', value: 'yes'},
      {label: 'No', value: 'no'}
    ],
    enableDisable: [
      {label: 'Enable', value: true},
      {label: 'Disable', value: false}
    ],
    timeOfDay: [
      {label: '00:00 (12:00a)', value: '00:00'},
      {label: '00:30 (12:30a)', value: '00:30'},
      {label: '01:00 (1:00a)', value: '01:00'},
      {label: '01:30 (1:30a)', value: '01:30'},
      {label: '02:00 (2:00a)', value: '02:00'},
      {label: '02:30 (2:30a)', value: '02:30'},
      {label: '03:00 (3:00a)', value: '03:00'},
      {label: '03:30 (3:30a)', value: '03:30'},
      {label: '04:00 (4:00a)', value: '04:00'},
      {label: '04:30 (4:30a)', value: '04:30'},
      {label: '05:00 (5:00a)', value: '05:00'},
      {label: '05:30 (5:30a)', value: '05:30'},
      {label: '06:00 (6:00a)', value: '06:00'},
      {label: '06:30 (6:30a)', value: '06:30'},
      {label: '07:00 (7:00a)', value: '07:00'},
      {label: '07:30 (7:30a)', value: '07:30'},
      {label: '08:00 (8:00a)', value: '08:00'},
      {label: '08:30 (8:30a)', value: '08:30'},
      {label: '09:00 (9:00a)', value: '09:00'},
      {label: '09:30 (9:30a)', value: '09:30'},
      {label: '10:00 (10:00a)', value: '10:00'},
      {label: '10:30 (10:30a)', value: '10:30'},
      {label: '11:00 (11:00a)', value: '11:00'},
      {label: '11:30 (11:30a)', value: '11:30'},
      {label: '12:00 (12:00p)', value: '12:00'},
      {label: '12:30 (12:30p)', value: '12:30'},
      {label: '13:00 (1:00p)', value: '13:00'},
      {label: '13:30 (1:30p)', value: '13:30'},
      {label: '14:00 (2:00p)', value: '14:00'},
      {label: '14:30 (2:30p)', value: '14:30'},
      {label: '15:00 (3:00p)', value: '15:00'},
      {label: '15:30 (3:30p)', value: '15:30'},
      {label: '16:00 (4:00p)', value: '16:00'},
      {label: '16:30 (4:30p)', value: '16:30'},
      {label: '17:00 (5:00p)', value: '17:00'},
      {label: '17:30 (5:30p)', value: '17:30'},
      {label: '18:00 (6:00p)', value: '18:00'},
      {label: '18:30 (6:30p)', value: '18:30'},
      {label: '19:00 (7:00p)', value: '19:00'},
      {label: '19:30 (7:30p)', value: '19:30'},
      {label: '20:00 (8:00p)', value: '20:00'},
      {label: '20:30 (8:30p)', value: '20:30'},
      {label: '21:00 (9:00p)', value: '21:00'},
      {label: '21:30 (9:30p)', value: '21:30'},
      {label: '22:00 (10:00p)', value: '22:00'},
      {label: '22:30 (10:30p)', value: '22:30'},
      {label: '23:00 (11:00p)', value: '23:00'},
      {label: '23:30 (11:30p)', value: '23:30'},
    ]
  };

  constructor(public navCtrl: NavController, private alertCtrl: AlertController, private viewApi: ViewProvider, public modalCtrl: ModalController, public events: Events) {
    this.intervalometer = this.viewApi.intervalometer;
    this.camera = this.viewApi.camera;
    this.frames = this.viewApi.timelapse.frames;
    this.motion = this.viewApi.motion;
    if(!this.intervalometer.program.loaded) this.intervalometer.getProgram();
    this.intervalometer.getStatus();
    this.motion.get();
    events.subscribe('intervalometer.program', () => {
      this.updateKeyframeChart();
    })
    events.subscribe('intervalometer.stopped', () => {
      console.log("event: intervalometer stopped");
      this.intervalometer.getProgram();
      this.motion.get();
    })
    events.subscribe('motion.updated', () => {
      console.log("event: motion updated");
      this.updateKeyframeChart();
    })
  }

  ionViewDidLoad() {
      if(!this.chartCanvas) return;
      this.chart = new Chart(this.chartCanvas.nativeElement, {

          type: 'line',
          data: this.intervalometer.getChartData(0, false),
          options: {
            animation: false,
            legend: {
              display: false,
            },
            scales: {
              yAxes: [{
                display: false,
                ticks: {
                  beginAtZero:true
                }
              }],
              xAxes: [{
                type: 'time',
                distribution: 'linear',
                time: {
                    round: 'second',
                    displayFormats: {
                        second: 'H:mm:ss',
                        minute: 'H:mm',
                        hour: 'H:mm',
                        day: 'H:mm'
                    }
                }
              }]
            }
          }

      });
  }

  saveProgram(program) {
    this.intervalometer.saveProgram(program);
  }

  start(program) {
    this.viewMode = "status";
    this.camera.histogram = [];
    this.intervalometer.preview.image = null;
    this.intervalometer.status.running = true;
    this.intervalometer.status.message = "loading...";
    this.camera.status = "";
    for(let key in this.intervalometer.program.axes) {
      console.log("checking axis", key);
      if(this.allowedNonMotorAxes.indexOf(key) !== -1) continue;
      let found = false;
      for(let i = 0; i < this.motion.axis.length; i++) {
        if(this.motion.axis[i].id == key && this.motion.axis[i].connected) {
          found = true;
          break;
        }
      }
      if(found) continue;
      this.intervalometer.program.axes[key] = null;
      delete this.intervalometer.program.axes[key];
      console.log(" -- deleted", key);
    }
    this.intervalometer.start(program);
  }

  stop() {
    let confirm = this.alertCtrl.create({
      title: 'Stop time-lapse?',
      message: 'this will stop the currently running time-lapse as soon as the current frame is completed.',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            console.log('Stop canceled');
          }
        },
        {
          text: 'STOP',
          handler: () => {
            this.intervalometer.stop();
          }
        }
      ]
    });
    confirm.present();
  }

  startFrame() {
    if(this.viewApi.timelapse.frames.current) {
      if(this.viewApi.intervalometer.preview.playing) this.togglePlay();
      this.viewApi.intervalometer.preview.frameIndex = 0;
    }
  }

  endFrame() {
    if(this.viewApi.timelapse.frames.current) {
      if(this.viewApi.intervalometer.preview.playing) this.togglePlay();
      this.viewApi.intervalometer.preview.frameIndex = this.viewApi.timelapse.frames.current.length - 1;
    }
  }

  previousFrame() {
    if(this.viewApi.timelapse.frames.current) {
      if(this.viewApi.intervalometer.preview.playing) this.togglePlay();
      if(this.viewApi.intervalometer.preview.frameIndex > 0) {
        this.viewApi.intervalometer.preview.frameIndex--;
      }
    }
  }

  nextFrame() {
    if(this.viewApi.timelapse.frames.current) {
      if(this.viewApi.intervalometer.preview.playing) this.togglePlay();
      if(this.viewApi.intervalometer.preview.frameIndex < this.viewApi.timelapse.frames.current.length - 1) {
        this.viewApi.intervalometer.preview.frameIndex++;
      }
    }
  }

  updateFrame(event) {
    if(this.viewApi.timelapse.frames.current) {
      //if(clip.playing) this.togglePlay();
      if(this.viewApi.intervalometer.preview.frameIndex >= 0 && this.viewApi.intervalometer.preview.frameIndex < this.viewApi.timelapse.frames.current.length) {
        this.viewApi.intervalometer.preview.image = "data:image/jpeg;base64," + this.viewApi.timelapse.frames.current[this.viewApi.intervalometer.preview.frameIndex];
      }
    }
  }

  togglePlay() {
    if(this.viewApi.timelapse.frames.current && this.viewApi.intervalometer.status.frames == this.viewApi.timelapse.frames.current.length) {
      if(this.viewApi.intervalometer.preview.playing) {
        clearInterval(this.intervalHandle);
        this.viewApi.intervalometer.preview.playing = false;
      } else {
        this.viewApi.intervalometer.preview.playing = true;
        if(this.viewApi.intervalometer.preview.frameIndex >= this.viewApi.timelapse.frames.current.length -1) this.viewApi.intervalometer.preview.frameIndex = 0;
        this.intervalHandle = setInterval(() => {
          if(this.viewApi.intervalometer.preview.frameIndex >= this.viewApi.timelapse.frames.current.length - 1) return this.togglePlay();
          this.viewApi.intervalometer.preview.frameIndex++;
        }, 1000/30);
      }
    } else {
      if(!this.viewApi.intervalometer.preview.loadingFrames) this.viewApi.timelapse.getCurrentFrames();
    }
  }

  focusOffset(steps) {
    this.viewApi.intervalometer.status.focusDiffNew += steps;
    this.viewApi.intervalometer.moveFocus(steps);
  }

  sendSchedule() {
    let newSchedule = {
      schedMonday: this.intervalometer.program.schedMonday,
      schedTuesday: this.intervalometer.program.schedTuesday,
      schedWednesday: this.intervalometer.program.schedWednesday,
      schedThursday: this.intervalometer.program.schedThursday,
      schedFriday: this.intervalometer.program.schedFriday,
      schedSaturday: this.intervalometer.program.schedSaturday,
      schedSunday: this.intervalometer.program.schedSunday,
      schedStart: this.intervalometer.program.schedStart,
      schedStop: this.intervalometer.program.schedStop   
    }
    console.log("updateProgram:", newSchedule);
    this.viewApi.intervalometer.updateProgram(newSchedule);
  }

  doSelect(list, object, key, title) {
    let alert = this.alertCtrl.create();
    alert.setTitle(title || "select an option");

    for(let i = 0; i < list.length; i++) {
      let item = list[i];
      alert.addInput({
        type: 'radio',
        label: item.label,
        value: item.value,
        checked: (item.value == object[key])
      });
    }

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: (data: any) => {
        console.log('Radio data:', data);
        object[key] = data;
      }
    });

    alert.present();
  }

  doNumber(name, object, key) {
    let alert = this.alertCtrl.create();
    alert.setTitle(name || "select an option");

    alert.addInput({
      type: 'number',
      value: object[key]
    });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: (data: any) => {
        console.log('Number data:', data);
        object[key] = data[0];
      }
    });

    alert.present();
  }

  updateKeyframeChart() {
    if(!this.chart) return;
    this.chart.data = this.intervalometer.getChartData(0, false);
    this.chart.update();
  }

  openKeyframe(index) {
    let modal = this.modalCtrl.create(KeyframeModalContentPage, {keyframeIndex: index});
    modal.onDidDismiss(data => {
      this.updateKeyframeChart();
    });
    modal.present();
  }

}


@Component({
  selector: 'page-keyframe',
  templateUrl: 'keyframe.modal.html'
})
export class KeyframeModalContentPage {
  motion: any = {};
  keyframes: any = [];
  camera: any = {};
  axes: any = {};
  program: any = {};
  intervalometer: any = {};
  colors: any = {};
  posSeconds: number = 0;
  noUpdatePosSeconds: number = 0;

  xSlide = 0;

  f: any = {
    pos: 0
  }

  @ViewChild('chart') chartCanvas;
  chart :any;

  lengthString: any = "";
  selectionString: any = "";

  constructor(
    public params: NavParams,
    public viewCtrl: ViewController,
    private viewApi: ViewProvider,
    private alertCtrl: AlertController,
    private modalCtrl: ModalController
  ) {
    this.motion = this.viewApi.motion;
    if(!this.viewApi.intervalometer.program.axes) this.viewApi.intervalometer.program.axes = {};
    this.axes = this.viewApi.intervalometer.program.axes;
    this.program = this.viewApi.intervalometer.program;
    this.intervalometer = this.viewApi.intervalometer;
    this.camera = this.viewApi.camera;
    this.colors = this.viewApi.colors;
    setTimeout(()=>{
      this.updateLengthStrings(0, 0);
      this.refreshChart();
    });
  }

  doDuration(name, object, key) {
    let alert = this.alertCtrl.create();
    alert.setTitle(name || "enter a duration");

    if(object === false) object = this;

    let val = object[key];
    let s = val % 60;
    val -= s;
    val /= 60;
    let m = val % 60;
    val -= m;
    val /= 60;
    let h = val;

    alert.addInput({
      placeholder: 'hours',
      type: 'number',
      value: h.toString()
    });
    alert.addInput({
      placeholder: 'minutes',
      type: 'number',
      value: m.toString()
    });
    alert.addInput({
      placeholder: 'seconds',
      type: 'number',
      value: s.toString()
    });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: (data: any) => {
        object[key] = (parseInt(data[0])||0) * 3600 + (parseInt(data[1])||0) * 60 + (parseInt(data[2])||0);
        console.log('Seconds data:', data, object[key]);
        if(this.posSeconds > this.program.durationSeconds) this.posSeconds = this.program.durationSeconds;
        let posSeconds = this.posSeconds;
        let newSlidePos = Math.round((this.posSeconds / this.program.durationSeconds) * 100);
        console.log("jumping to ", this.posSeconds, "seconds = ", newSlidePos);
        this.updateLengthStrings(newSlidePos, (this.xSlide == newSlidePos) ? 1 : 2);
        this.xSlide = newSlidePos;
        this.posSeconds = posSeconds;
        this.refreshChart();
      }
    });

    alert.present();
  }

  updateLengthStrings(percent:number, noUpdatePosSeconds) {
    if(!percent) percent = 0;
    let h = Math.floor(this.program.durationSeconds / 3600);
    let m = Math.floor((this.program.durationSeconds - h * 3600) / 60);
    let s = this.program.durationSeconds % 60;
    this.lengthString = "";
    if(h > 0) this.lengthString = h.toString() + "h";
    if(m < 10) this.lengthString += "0";
    this.lengthString += m.toString() + "m";
    if(!h||s) {
      if(s < 10) this.lengthString += "0";
      this.lengthString += s.toString() + "s";      
    }

    if(!noUpdatePosSeconds && !this.noUpdatePosSeconds) this.posSeconds = Math.round(this.program.durationSeconds * (percent / 100));    
    h = Math.floor(this.posSeconds / 3600);
    m = Math.floor((this.posSeconds - h * 3600) / 60);
    s = this.posSeconds % 60;
    this.selectionString = "";
    if(h > 0) this.selectionString = h.toString() + "h";
    if(m < 10) this.selectionString += "0";
    this.selectionString += m.toString() + "m";
    if(s < 10) this.selectionString += "0";
    this.selectionString += s.toString() + "s";
    this.noUpdatePosSeconds = noUpdatePosSeconds == 2 ? true : false;
  }

  refreshChart() {
    for(let m in this.axes) {
      if(this.axes[m].type != 'keyframe') continue;
      if(!this.axes[m].kf) this.axes[m].kf = [];
      let index = this.motion.getAxisIndex(m);
      if(!this.motion.axis[index]) continue;
      this.motion.axis[index].prevKeyframe = null;
      this.motion.axis[index].nextKeyframe = null;
      this.motion.axis[index].selected = false;
      this.motion.axis[index].atStartOrEnd = false;
      for(let i = 0; i < this.axes[m].kf.length; i++) {
        if(Math.round((this.axes[m].kf[i].seconds / this.program.durationSeconds) * 100) == Math.round((this.posSeconds / this.program.durationSeconds) * 100)) {
          this.motion.axis[index].selected = true;          
          this.posSeconds = this.axes[m].kf[i].seconds;
          this.updateLengthStrings(this.xSlide, 1);
          this.motion.axis[index].atPos = (Math.round(this.motion.axis[index].pos) == Math.round(this.axes[m].kf[i].position));
          if(this.posSeconds == this.program.durationSeconds || this.posSeconds == 0) this.motion.axis[index].atStartOrEnd = true;
        } else {
          if(Math.round(this.axes[m].kf[i].seconds) < this.posSeconds && (this.motion.axis[index].prevKeyframe === null || this.axes[m].kf[i].seconds > this.motion.axis[index].prevKeyframe)) this.motion.axis[index].prevKeyframe = this.axes[m].kf[i].seconds;
          if(Math.round(this.axes[m].kf[i].seconds) > this.posSeconds && (this.motion.axis[index].nextKeyframe === null || this.axes[m].kf[i].seconds < this.motion.axis[index].nextKeyframe)) this.motion.axis[index].nextKeyframe = this.axes[m].kf[i].seconds;
        }
      }
    }
    if(this.chart) {
      this.chart.data = this.intervalometer.getChartData(this.posSeconds, true);
      this.chart.update();
    }
  }

  updateX(x) {
    let newSlidePos = Math.round((x / this.program.durationSeconds) * 100);
    console.log("jumping to ", x, "seconds = ", newSlidePos);
    this.updateLengthStrings(newSlidePos, (this.xSlide == newSlidePos) ? 1 : 2);
    this.xSlide = newSlidePos;
    this.posSeconds = x;
    this.refreshChart();
  }

  moveToCurrent(axisId) {
    let index = this.motion.getAxisIndex(axisId);
    if(!this.motion.axis[index]) return;
    let currentPos = this.motion.axis[index].pos;
    for(let i = 0; i < this.axes[axisId].kf.length; i++) {
      if(Math.round(this.axes[axisId].kf[i].seconds) == this.posSeconds) {
        let targetPos = this.axes[axisId].kf[i].position
        console.log("moving", axisId, "from", currentPos, "to", targetPos);
        let steps = targetPos - currentPos;
        this.motion.move(axisId, steps, true, (err, position) => {
          console.log(axisId + " position " + position);
          console.log("this.motion.axis", this.motion.axis);
          this.program[axisId + 'Pos'] = position;
          this.refreshChart();
        });
        break;
      }
    }
  }
  
  moveAxis(axisId, speed) {
    speed /= 10;
    if(speed < -1) speed = -1;
    if(speed > 1) speed = 1;
    console.log("moving", axisId, "at speed", speed);
    this.motion.moveConstant(axisId, speed, (err, position) => {
      console.log(axisId + " position " + position);
      console.log("this.motion.axis", this.motion.axis);
      this.program[axisId + 'Pos'] = position;
      this.refreshChart();
    });
  }

  setHomePos() {
    let moved = false;
    for(let axisId in this.axes) {
      if(this.axes[axisId].kf && this.axes[axisId].kf.length > 0) {
        let homePos = this.axes[axisId].kf[0].position;
        if(homePos != 0) {
          for (let i = 0; i < this.axes[axisId].kf.length; i++) {
            this.axes[axisId].kf[i].position -= homePos;
          }
          moved = true;
          let curPos = this.motion.axis[this.motion.getAxisIndex(axisId)].pos || 0;
          let newPos = curPos - homePos;
          this.motion.setAxisPosition(axisId, newPos);
          console.log("updating", axisId, "position to", newPos);
          this.motion.axis[this.motion.getAxisIndex(axisId)].pos = newPos;
        }
      }
    }
  }

  addKeyframe(axisId) {
    this.axes[axisId].kf.push({
      seconds: this.posSeconds,
      position: this.motion.axis[this.motion.getAxisIndex(axisId)].pos || 0
    })
    this.setHomePos();
    this.refreshChart();
  }

  removeKeyframe(axisId) {
    for(let i = 0; i < this.axes[axisId].kf.length; i++) {
      if(Math.round(this.axes[axisId].kf[i].seconds) == this.posSeconds) {
        this.axes[axisId].kf.splice(i, 1);
      }
    }
    this.refreshChart();
  }

  updateKeyframe(axisId) {
    console.log("updateKeyframe", axisId);
    for(let i = 0; i < this.axes[axisId].kf.length; i++) {
      if(Math.round(this.axes[axisId].kf[i].seconds) == this.posSeconds) {
        this.axes[axisId].kf[i].position = this.motion.axis[this.motion.getAxisIndex(axisId)].pos || 0;
        console.log("updateKeyframe -- found", axisId);
        this.setHomePos();
        break;
      }
    }
    this.refreshChart();
  }

  setup(axis) {
    let modal = this.modalCtrl.create(MotionModalContentPage, {axisId: axis.id});
    modal.present();
  }

  ionViewDidLoad() {

      this.chart = new Chart(this.chartCanvas.nativeElement, {

          type: 'line',
          data: this.intervalometer.getChartData(this.posSeconds, true),
          options: {
            animation: false,
            legend: {
              display: false,
            },
            scales: {
              yAxes: [{
                display: false,
                ticks: {
                  beginAtZero:true
                }
              }],
              xAxes: [{
                type: 'time',
                distribution: 'linear',
                time: {
                    round: 'second',
                    displayFormats: {
                        second: 'H:mm:ss',
                        minute: 'H:mm',
                        hour: 'H:mm',
                        day: 'H:mm'
                    }
                }
              }]
            }
          }

      });
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }
}






